html,
body {
  margin: 0;
  padding: 0;
  font-family: "Codec Cold Trial", sans-serif;
}

/** fonts */

@font-face {
  font-family: "Codec Cold Trial";
  font-style: normal;
  font-weight: normal;
  src: local("Codec Cold Trial Regular"),
    url("../assets/fonts/Codec-Cold-Regular-trial.woff") format("woff");
}

:root {
  --color-primary: #60c1c7;
  --color-primary--darken: #45b2b9;
  --color-secondary: #484851;
  --color-secondary--darken: #000000;
  --big-shadow: -5px 20px 30px rgba(168, 176, 190, 0.5);
  --small-shadow: -1px 2px 4px rgba(168, 176, 190, 0.5);
}

a.link {
  color: var(--color-primary);
  text-decoration: none;
}

a.link:hover {
  color: var(--color-primary--darken);
}

.btn {
  display: inline-flex;
  position: relative;
  overflow: hidden;
  font-weight: bold;
  font-size: 16px;
  line-height: 16px;
  align-items: center;
  text-align: center;
  color: #fff;
  padding: 18px 36px 16px 36px;
  border-radius: 40px;
  border: 0;
  transition: all 0.25s ease 0s;
  cursor: pointer;
  text-decoration: none;
}
.btn:before,
.btn:after {
  content: "";
  top: 0;
  left: 0;
  position: absolute;
}
.btn:before {
  height: 100%;
  width: 100%;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  background-color: var(--color-primary);
  z-index: -2;
}
.btn:after {
  height: 100%;
  width: 100%;
  height: 0%;
  left: 50%;
  top: 50%;
  width: 150%;
  z-index: -1;
  transition: all 0.25s ease 0s;
  background: #45b2b9;
  opacity: 0.5;
  transform: translateX(-50%) translateY(-50%);
  z-index: -1;
}
.btn:hover::after,
.btn:active::after,
.btn:focus::after {
  height: 140%;
  opacity: 1;
}

.font-fallback {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
}

button {
  white-space: nowrap;
}
@media (max-width: 768px) {
  .hide-tablet {
    display: none !important;
  }
}
@media (max-width: 425px) {
  .hide-modile {
    display: none !important;
  }
}
